<template>
  <div class="container py-5">
    <!-- 餐廳資訊頁 RestaurantDetail -->
    <RestaurantDetail :initial-restaurant="restaurant"/>
    <hr>
    <!-- 餐廳評論 RestaurantComments -->
    <RestaurantComments :restaurant-comments="restaurantComments" @after-delete-comment="afterDeleteComment"/>
    <!-- 新增評論 CreateComment -->
    <CreateComment :restaurant-id="restaurant.id" @after-create-comment="afterCreateComment"/>
  </div>
</template>

<script>
import RestaurantDetail from '../components/RestaurantDetail.vue'
import RestaurantComments from '../components/RestaurantComments.vue'
import CreateComment from '../components/CreateComment.vue'
import restaurantAPI from '../apis/restaurants'
import { Toast } from '../utils/helpers'
import { mapState } from 'vuex'
import usersAPI from '../apis/users'

export default {
    data() {
        return {
            restaurant: {
                id: -1,
                name: '',
                categoryName: '',
                image: '',
                openingHours: '',
                tel: '',
                address: '',
                description: '',
                isFavorited: false,
                isLiked: false
            },
            restaurantComments: [],
        }
    },
    created() {
        const { id: restaurantId } = this.$route.params
        this.fetchRestaurant(restaurantId)
    },
    methods: {
        async fetchRestaurant (restaurantId) {
            try {
                const { data } = await restaurantAPI.getRestaurant({ restaurantId })

                this.restaurant = {
                    id: data.restaurant.id,
                    name: data.restaurant.name,
                    categoryName: data.restaurant.Category.name,
                    image: data.restaurant.image,
                    openingHours: data.restaurant.opening_hours,
                    tel: data.restaurant.tel,
                    address: data.restaurant.address,
                    description: data.restaurant.description,
                    isFavorited: data.isFavorited,
                    isLiked: data.isLiked,
                }
                this.restaurantComments = data.restaurant.Comments
            } catch(err) {
                Toast.fire({
                    icon: 'error',
                    title: '無法取得餐廳資料，請稍後再試'
                })
            }
            
        },
        async afterDeleteComment(commentId) {
            try {
                const { data } = await usersAPI.deleteComment({ commentId })
                if(data.status !== 'success') {
                    throw new Error(data.message)
                }
                Toast.fire({
                    icon: 'success',
                    title: '評論刪除成功'
                })
                this.fetchRestaurant(this.restaurant.id)
            } catch(err) {
                Toast.fire({
                    icon: 'error',
                    title: '無法刪除評論，請稍後再試'
                })
                console.log('error', err)
            }
        },
        async afterCreateComment(payload) {
            try {
                const { restaurantId, text } = payload
                const commentData = JSON.stringify({ restaurantId, text })
                const { data } = await usersAPI.createComment({ commentData })
                if(data.status !== 'success') {
                    throw new Error(data.message)
                }
                Toast.fire({
                    icon: 'success',
                    title: '評論新增成功'
                })
                this.fetchRestaurant(restaurantId)
            } catch(err) {
                Toast.fire({
                    icon: 'error',
                    title: '無法新增評論，請稍後再試'
                })
                console.log('error', err)
            }
        }
    },
    components: {
        RestaurantDetail,
        RestaurantComments,
        CreateComment,
    },
    beforeRouteUpdate(to, from, next) {
        const { id } = to.params
        this.fetchRestaurant(id)
        next()
    },
    computed: {
        ...mapState(['currentUser'])
    }
}
</script>